@import "../../scss/_sass_vars.scss";

@keyframes fakeChatWindowAnimation {
  0% {
    opacity: 0.1;
    transform: scale(1) translateX(-50%);
  }
  100% {
    opacity: 1;
    transform: scale(1.2) translateX(0);
  }
}

.fakeChatWindowWrapper {
  position: absolute;
  top: 50%;
  right: 110px;
  pointer-events: none;
  transform: translateY(-50%);
  

  @media screen and (max-width: $mobileBreakpoint) {
    position: static;
  }

  .fakeChatWindow {
    width: 330px;
    min-height: 300px;
    border-style: solid;
    border-width: 0;
    border-radius: 10px;
    border-color: transparent;
    font-size: 14px;
    overflow: hidden;
    box-shadow: 7px 5px 13px 0px black;
    animation: fakeChatWindowAnimation $animationSettings;
    transition: border-color 250ms linear, border-radius 250ms linear,
      border-width 250ms linear;
    animation-fill-mode: forwards;

    &.styleChanged {
      border-width: 5px;
      border-color: black;
      border-radius: 0;
    }
  }

  .fakeChatHeader {
    position: relative;
    display: flex;
    justify-content: center;
    background-color: rgb(77, 23, 28);
    padding: 5px;
    font-family: system-ui, Arial, sans-serif;
    font-weight: bold;
    color: white;
  }

  .fakeChatContent {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    padding: 10px;
  }

  .fakeInputWrapper {
    display: flex;
    gap: 5px;
    width: 100%;
    border-radius: 5px;
    padding: 10px 12px;
    border: 1px solid grey;

    & button {
      border: none;
      background-color: transparent;
      padding: 0;
      cursor: pointer;
    }
    & input {
      flex-grow: 1;
      border: none;
      outline: none;
    }
  }
}
