@import "../scss/sass_vars";

.appMain {
  position: relative;
  height: 100vh;
  background-color: var(--main-background-color);
}

.main {
  position: relative;
  height: calc(100vh - 50px);
  padding: 0 5vw;
  overflow-x: hidden;
  background-image: linear-gradient(to top, #37ecba 0%, #72afd3 100%);
  perspective: 800px;
}

:global(.page) {
  display: flex;
  height: 100%;
  padding: 30px 0;
}

:global(.pageContent) {
  height: 100%;
  width: 100%;
  overflow: visible;
}

:global(.hintText) {
  font-size: 12px;
  color: var(--main-text-color-alpha);
}
